.link {
  position: relative;
  display: inline-block;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: color 0.3s;
}
.link__text {
  position: relative;
}
.link::before {
  content: "";
  position: absolute;
  top: -2px;
  right: -6px;
  bottom: -3px;
  left: -6px;
  background-color: var(--bright-yellow);
  transform: scaleX(0);
  transition: transform var(--short);
}