.link {
  position: relative;
  display: inline-block;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: color .3s;

  &__text {
    position: relative;
  }

  &::before {
    content: "";
    position: absolute;
    top: -2px;
    right: -6px;
    bottom: -3px;
    left: -6px;
    background-color: var(--bright-yellow);
    transform: scaleX(0);
    transition: transform var(--short);
  }
}